import React from 'react';
import { Box, Paper, Typography, Button, useTheme, useMediaQuery, Link } from '@mui/material';
import { BarChart, TrendingUp, Info as InfoIcon } from '@mui/icons-material';

const Home = ({ onGetStarted, onViewExamples, onViewAbout }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm')); // < 600px
  const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md')); // 600px - 960px

  return (
    <Box sx={{
      minHeight: '100vh',
      width: '100vw',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      bgcolor: 'transparent',
      overflow: 'auto',
      position: 'relative',
      backgroundSize: isMobile ? '200% 200%' : '400% 400%',
      animation: 'gradientBG 15s ease infinite',
      '@keyframes gradientBG': {
        '0%': { backgroundPosition: '0% 50%' },
        '50%': { backgroundPosition: '100% 50%' },
        '100%': { backgroundPosition: '0% 50%' },
      },
    }}>
      <Paper elevation={12} sx={{
        p: isMobile ? 2 : isTablet ? 3 : 4,
        borderRadius: 3,
        width: '100%',
        maxWidth: isMobile ? '95%' : isTablet ? '85%' : '800px',
        bgcolor: theme.palette.background.paper,
        boxShadow: '0 15px 30px rgba(0, 0, 0, 0.2)',
        color: theme.palette.text.primary,
        transition: 'transform 0.3s, box-shadow 0.3s',
        '&:hover': { transform: 'scale(1.02)', boxShadow: '0 20px 40px rgba(0, 0, 0, 0.3)' },
        my: isMobile ? 2 : 4,
      }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 3, flexWrap: 'wrap' }}>
          <BarChart sx={{ color: theme.palette.primary.main, fontSize: isMobile ? 30 : 40, mr: 1 }} />
          <Typography
            variant={isMobile ? 'h4' : 'h2'}
            align="center"
            gutterBottom
            sx={{
              color: theme.palette.primary.main,
              fontWeight: 'bold',
              textShadow: '2px 2px 4px rgba(0, 0, 0, 0.1)',
              fontFamily: 'Roboto, sans-serif',
              fontSize: {
                xs: '2rem',   // Mobile: Increased from 1.75rem to 2rem
                sm: '2.5rem', // Tablet: Slightly larger
                md: '3rem'    // Desktop: Increased for a bigger heading
              },
            }}
          >
            DataTrain Visualizer
          </Typography>
          <TrendingUp sx={{ color: theme.palette.secondary.main, fontSize: isMobile ? 30 : 40, ml: 1 }} />
        </Box>
        <Typography
          variant={isMobile ? 'body1' : 'h5'}
          align="center"
          gutterBottom
          sx={{
            color: theme.palette.text.secondary,
            mb: 4,
            lineHeight: 1.6,
            fontFamily: 'Roboto, sans-serif',
            textShadow: '1px 1px 2px rgba(0, 0, 0, 0.05)',
            fontSize: isMobile ? '1rem' : 'inherit',
          }}
        >
          Unleash the power of your data with cutting-edge machine learning and interactive visualizations. Upload your CSV, train models, and explore insights like never before.
        </Typography>
        <Box sx={{
          display: 'flex',
          flexDirection: isMobile ? 'column' : 'row',
          justifyContent: 'center',
          gap: isMobile ? 1.5 : 2,
          alignItems: 'center',
        }}>
          <Button
            variant="contained"
            color="primary"
            onClick={onGetStarted}
            startIcon={<BarChart />}
            sx={{
              padding: isMobile ? '8px 16px' : '12px 24px',
              fontSize: isMobile ? '12px' : '16px',
              borderRadius: 2,
              background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.primary.light})`,
              boxShadow: '0 6px 15px rgba(25, 118, 210, 0.4)',
              color: theme.palette.text.primary,
              '&:hover': {
                background: `linear-gradient(45deg, ${theme.palette.primary.dark}, ${theme.palette.primary.main})`,
                transform: 'scale(1.05)',
                transition: 'transform 0.3s, background 0.3s',
              },
              textTransform: 'none',
              width: isMobile ? '100%' : 'auto',
              maxWidth: '300px',
            }}
          >
            Get Started
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            onClick={onViewExamples}
            startIcon={<TrendingUp />}
            sx={{
              padding: isMobile ? '8px 16px' : '12px 24px',
              fontSize: isMobile ? '12px' : '16px',
              borderRadius: 2,
              borderColor: theme.palette.secondary.main,
              color: theme.palette.secondary.main,
              '&:hover': {
                borderColor: theme.palette.secondary.main,
                backgroundColor: 'rgba(74, 20, 140, 0.1)',
                transform: 'scale(1.05)',
                transition: 'transform 0.3s, background 0.3s',
              },
              textTransform: 'none',
              width: isMobile ? '100%' : 'auto',
              maxWidth: '300px',
            }}
          >
            View Examples
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            onClick={onViewAbout}
            startIcon={<InfoIcon />}
            sx={{
              padding: isMobile ? '8px 16px' : '12px 24px',
              fontSize: isMobile ? '12px' : '16px',
              borderRadius: 2,
              borderColor: theme.palette.secondary.main,
              color: theme.palette.secondary.main,
              '&:hover': {
                borderColor: theme.palette.secondary.main,
                backgroundColor: 'rgba(74, 20, 140, 0.1)',
                transform: 'scale(1.05)',
                transition: 'transform 0.3s, background 0.3s',
              },
              textTransform: 'none',
              width: isMobile ? '100%' : 'auto',
              maxWidth: '300px',
            }}
          >
            About Us
          </Button>
        </Box>

        {/* LinkedIn Link */}
        <Box sx={{ mt: 4, width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Link
            href="https://www.linkedin.com/in/abdullah-azhar23/"
            target="_blank"
            rel="noopener noreferrer"
            sx={{
              color: theme.palette.primary.main,
              textDecoration: 'none',
              fontSize: isMobile ? '12px' : '16px',
              fontWeight: 'bold',
              display: 'flex',
              alignItems: 'center',
              '&:hover': {
                color: theme.palette.primary.dark,
                textDecoration: 'underline',
                transform: 'scale(1.05)',
                transition: 'transform 0.3s, color 0.3s',
              },
            }}
          >
            Connect on LinkedIn
          </Link>
        </Box>
      </Paper>

      {/* Footer */}
      <Typography
        variant="body2"
        align="center"
        sx={{
          width: '100%',
          color: theme.palette.text.secondary,
          opacity: 0.7,
          fontFamily: 'Roboto, sans-serif',
          py: 2,
          fontSize: isMobile ? '0.75rem' : '0.875rem',
        }}
      >
        Powered by Abdullah © 2025
      </Typography>
    </Box>
  );
};

export default Home;