import React from 'react';
import { Box, Paper, Typography, Button, useTheme, useMediaQuery, Grid, List, ListItem, ListItemIcon, ListItemText, Link } from '@mui/material';
import { Info, Code, BarChart, People } from '@mui/icons-material';

const About = ({ onBackToHome }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm')); // < 600px
  const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md')); // 600px - 960px
  const isDesktop = useMediaQuery(theme.breakpoints.up('md')); // > 960px

  return (
    <Box sx={{
      minHeight: '100vh',
      width: '100vw', // Full screen width
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      bgcolor: 'transparent',
      overflow: 'auto', // Changed to 'auto' to allow scrolling on small screens
      position: 'relative',
      backgroundSize: '400% 400%',
      animation: 'gradientBG 15s ease infinite',
      '@keyframes gradientBG': {
        '0%': { backgroundPosition: '0% 50%' },
        '50%': { backgroundPosition: '100% 50%' },
        '100%': { backgroundPosition: '0% 50%' },
      },
    }}>
      <Paper elevation={12} sx={{
        p: isMobile ? 2 : isTablet ? 3 : 4,
        borderRadius: 3,
        width: '100%', // Changed to 100% for better scaling
        maxWidth: isDesktop ? '1200px' : isTablet ? '90%' : '95%', // Adjusted for responsiveness
        bgcolor: theme.palette.background.paper,
        boxShadow: '0 15px 30px rgba(0, 0, 0, 0.2)',
        color: theme.palette.text.primary,
        transition: 'transform 0.3s, box-shadow 0.3s',
        '&:hover': { transform: 'scale(1.02)', boxShadow: '0 20px 40px rgba(0, 0, 0, 0.3)' },
        margin: '0 auto',
        position: 'relative',
        my: isMobile ? 2 : 4, // Added vertical margin for spacing
      }}>
        <Typography
          variant="h2"
          align="center"
          sx={{
            color: theme.palette.primary.main,
            fontWeight: 'bold',
            mb: 4,
            textShadow: '2px 2px 4px rgba(0, 0, 0, 0.1)',
            fontFamily: 'Roboto, sans-serif',
            animation: 'fadeIn 1s ease-in',
            '@keyframes fadeIn': {
              '0%': { opacity: 0, transform: 'translateY(-20px)' },
              '100%': { opacity: 1, transform: 'translateY(0)' },
            },
            fontSize: isMobile ? '2rem' : '3rem', // Scaled down for mobile, kept reasonable for desktop
          }}
        >
          About DataTrain Visualizer
        </Typography>

        <Grid container spacing={isMobile ? 2 : 4}>
          <Grid item xs={12} md={6}>
            <Typography
              variant="h5"
              gutterBottom
              sx={{
                color: theme.palette.text.primary,
                fontWeight: 'bold',
                mb: 2,
                fontSize: isMobile ? '1.25rem' : '1.5rem', // Scaled for mobile
              }}
            >
              Our Mission
            </Typography>
            <Typography
              variant="body1"
              paragraph
              sx={{
                color: theme.palette.text.secondary,
                lineHeight: 1.6,
                mb: 3,
                fontSize: isMobile ? '0.9rem' : '1rem', // Adjusted for mobile readability
              }}
            >
              DataTrain Visualizer is designed to empower data enthusiasts and professionals with a powerful, user-friendly platform to unlock insights from their data. Using cutting-edge machine learning algorithms and interactive visualizations, we help you upload your CSV files, train models, and explore trends like never before. Our mission is to make data analysis accessible, intuitive, and impactful for everyone.
            </Typography>
            <Typography
              variant="h5"
              gutterBottom
              sx={{
                color: theme.palette.text.primary,
                fontWeight: 'bold',
                mb: 2,
                fontSize: isMobile ? '1.25rem' : '1.5rem',
              }}
            >
              What We Offer
            </Typography>
            <List sx={{ pl: 2 }}>
              {[
                'Advanced machine learning models (Regression, Classification, Clustering)',
                'Interactive graphs (Line, Bar, Scatter, Area)',
                'Real-time data visualization and customization',
                'Seamless CSV file uploads and model training',
                'Dark/light mode support for all-day usability',
              ].map((item, index) => (
                <ListItem key={index} disablePadding sx={{ py: isMobile ? 0.5 : 1 }}>
                  <ListItemIcon sx={{ minWidth: isMobile ? 24 : 30, color: theme.palette.primary.main }}>
                    <Info fontSize={isMobile ? 'small' : 'medium'} />
                  </ListItemIcon>
                  <ListItemText
                    primary={item}
                    sx={{ color: theme.palette.text.secondary, fontSize: isMobile ? '0.85rem' : '1rem' }}
                  />
                </ListItem>
              ))}
            </List>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography
              variant="h5"
              gutterBottom
              sx={{
                color: theme.palette.text.primary,
                fontWeight: 'bold',
                mb: 2,
                fontSize: isMobile ? '1.25rem' : '1.5rem',
              }}
            >
              Our Vision
            </Typography>
            <Typography
              variant="body1"
              paragraph
              sx={{
                color: theme.palette.text.secondary,
                lineHeight: 1.6,
                mb: 3,
                fontSize: isMobile ? '0.9rem' : '1rem',
              }}
            >
              We envision a world where data-driven decisions are simple and accessible to all. DataTrain Visualizer combines state-of-the-art technology with an engaging user experience, enabling individuals and businesses to harness the power of their data effortlessly. Whether you're a data scientist, student, or business analyst, our platform is your gateway to actionable insights.
            </Typography>
            <Typography
              variant="h5"
              gutterBottom
              sx={{
                color: theme.palette.text.primary,
                fontWeight: 'bold',
                mb: 2,
                fontSize: isMobile ? '1.25rem' : '1.5rem',
              }}
            >
              About the Creator
            </Typography>
            <Typography
              variant="body1"
              paragraph
              sx={{
                color: theme.palette.text.secondary,
                lineHeight: 1.6,
                mb: 3,
                fontSize: isMobile ? '0.9rem' : '1rem',
              }}
            >
              This project is proudly developed by Abdullah Azhar, a passionate developer dedicated to creating innovative solutions in data science and web development. With a keen interest in machine learning and user-centered design, Abdullah crafts DataTrain Visualizer to make data exploration intuitive and powerful for users worldwide.
            </Typography>
            {/* LinkedIn Link */}
            <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
              <Link
                href="https://www.linkedin.com/in/abdullah-azhar23/"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  color: theme.palette.primary.main,
                  textDecoration: 'none',
                  fontSize: isMobile ? '14px' : '16px',
                  fontWeight: 'bold',
                  display: 'flex',
                  alignItems: 'center',
                  '&:hover': {
                    color: theme.palette.primary.dark,
                    textDecoration: 'underline',
                    transform: 'scale(1.05)',
                    transition: 'transform 0.3s, color 0.3s',
                  },
                }}
              >
                Connect with Abdullah on LinkedIn
              </Link>
            </Box>
            <Box sx={{
              display: 'flex',
              flexDirection: isMobile ? 'column' : 'row', // Stack button on mobile
              justifyContent: 'center',
              gap: isMobile ? 1.5 : 2,
              mt: isMobile ? 2 : 3,
            }}>
              <Button
                variant="contained"
                color="primary"
                onClick={onBackToHome}
                startIcon={<BarChart />}
                sx={{
                  padding: isMobile ? '8px 16px' : '12px 24px', // Smaller padding on mobile
                  fontSize: isMobile ? '12px' : '16px', // Smaller font on mobile
                  borderRadius: 2,
                  background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.primary.light})`,
                  boxShadow: '0 6px 15px rgba(25, 118, 210, 0.4)',
                  color: theme.palette.text.primary,
                  '&:hover': {
                    background: `linear-gradient(45deg, ${theme.palette.primary.dark}, ${theme.palette.primary.main})`,
                    transform: 'scale(1.05)',
                    transition: 'transform 0.3s, background 0.3s',
                  },
                  textTransform: 'none',
                  animation: 'fadeIn 1s ease-in 0.5s backwards',
                  width: isMobile ? '100%' : 'auto', // Full width on mobile
                  maxWidth: '300px', // Cap width for consistency
                }}
              >
                Back to Home
              </Button>
            </Box>
          </Grid>
        </Grid>
      </Paper>
      {/* Footer */}
      <Typography
        variant="body2"
        align="center"
        sx={{
          width: '100%',
          color: theme.palette.text.secondary,
          opacity: 0.7,
          fontFamily: 'Roboto, sans-serif',
          py: 2, // Changed to padding for natural flow
          fontSize: isMobile ? '0.75rem' : '0.875rem', // Smaller on mobile
        }}
      >
        Powered by Abdullah © 2025
      </Typography>
    </Box>
  );
};

export default About;