// src/components/ViewExamples.js
import React, { useState, useEffect } from 'react';
import { Card, CardContent, Box, Typography, Button, Grid, CardMedia } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Graph from './Graph';
import GraphControls from './GraphControls';

const ViewExamples = ({ onBackToHome }) => {
  const theme = useTheme();
  const [graphType, setGraphType] = useState('line');
  const [graphData, setGraphData] = useState(null);
  const [graphTitle, setGraphTitle] = useState('Sample Training Results');
  const [xAxisLabel, setXAxisLabel] = useState('Sample Index');
  const [yAxisLabel, setYAxisLabel] = useState('Sample Values');
  const [actualColor, setActualColor] = useState('#1976d2');
  const [predictedColor, setPredictedColor] = useState('#d32f2f');
  const [pointSize, setPointSize] = useState(3);
  const [xTickStep, setXTickStep] = useState(5);
  const [yTickStep, setYTickStep] = useState(0.5);
  const [legendPosition, setLegendPosition] = useState('top');
  const [showGrid, setShowGrid] = useState(true);

  // Sample data for View Examples
  const sampleData = {
    train_x_data: Array.from({ length: 50 }, (_, i) => i),
    train_y_data: Array.from({ length: 50 }, () => Math.random() * 30 + 10),
    train_predictions: Array.from({ length: 50 }, () => Math.random() * 30 + 10),
  };

  // Image loading debugging
  useEffect(() => {
    const imageUrl = '/images/image.png';
    const img = new Image();
    img.src = `${window.location.origin}${imageUrl}`;
    img.onload = () => console.log('Image loaded successfully:', imageUrl);
    img.onerror = (e) => console.error('Image failed to load:', e, 'URL:', imageUrl);
  }, []);

  // Graph data preparation
  useEffect(() => {
    const ensureNumeric = (data) => data.map(val => (typeof val === 'number' && !isNaN(val) ? val : 0));
    const trainX = ensureNumeric(sampleData.train_x_data);
    const trainY = ensureNumeric(sampleData.train_y_data);
    const trainPred = ensureNumeric(sampleData.train_predictions);

    const totalPoints = trainX.length;
    const desiredPoints = Math.min(50, totalPoints);
    const step = Math.max(1, Math.floor(totalPoints / desiredPoints));

    const sampleDataFunc = (data) => data.filter((_, i) => i % step === 0);

    const sampledTrainX = sampleDataFunc(trainX);
    const sampledTrainY = sampleDataFunc(trainY);
    const sampledTrainPred = sampleDataFunc(trainPred);
    const trainLabels = sampledTrainX.map((x, i) => `Sample ${i * step}`);

    const prepareScatterData = (xValues, yValues) => xValues.map((x, i) => ({ x, y: yValues[i] }));

    let data;
    if (graphType === 'scatter') {
      data = {
        datasets: [
          { label: 'Actual (Train)', data: prepareScatterData(sampledTrainX, sampledTrainY), borderColor: '#1976d2', backgroundColor: '#1976d2', pointRadius: pointSize, type: 'scatter' },
          { label: 'Predicted (Train)', data: prepareScatterData(sampledTrainX, sampledTrainPred), borderColor: '#d32f2f', backgroundColor: '#d32f2f', pointRadius: pointSize, type: 'scatter' },
        ],
      };
    } else if (graphType === 'area') {
      data = {
        labels: trainLabels,
        datasets: [
          { label: 'Actual (Train)', data: sampledTrainY, borderColor: '#1976d2', backgroundColor: 'rgba(25, 118, 210, 0.2)', fill: true, pointRadius: pointSize, tension: 0.1, type: 'line' },
          { label: 'Predicted (Train)', data: sampledTrainPred, borderColor: '#d32f2f', backgroundColor: 'rgba(211, 47, 47, 0.2)', fill: true, pointRadius: pointSize, tension: 0.1, type: 'line' },
        ],
      };
    } else {
      data = {
        labels: trainLabels,
        datasets: [
          { label: 'Actual (Train)', data: sampledTrainY, borderColor: '#1976d2', backgroundColor: '#1976d2', fill: false, pointRadius: pointSize, tension: 0.1, type: graphType },
          { label: 'Predicted (Train)', data: sampledTrainPred, borderColor: '#d32f2f', backgroundColor: '#d32f2f', fill: false, pointRadius: pointSize, tension: 0.1, type: graphType },
        ],
      };
    }

    setGraphData({
      data,
      type: graphType === 'area' ? 'line' : graphType,
      options: {
        scales: {
          x: { ticks: { stepSize: xTickStep }, grid: { display: showGrid }, title: { display: true, text: xAxisLabel } },
          y: { ticks: { stepSize: yTickStep }, grid: { display: showGrid }, title: { display: true, text: yAxisLabel } },
        },
        plugins: { title: { display: true, text: graphTitle }, legend: { position: legendPosition } },
        elements: { line: { fill: graphType === 'area' } },
        maintainAspectRatio: false,
        responsive: true,
      },
    });
  }, [graphType, pointSize, xTickStep, yTickStep, legendPosition, showGrid]);

  const handleGraphTypeChange = (value) => setGraphType(value);
  const handleXTickStepChange = (value) => setXTickStep(value);
  const handleYTickStepChange = (value) => setYTickStep(value);
  const handleShowGridChange = (value) => setShowGrid(value);
  const handleLegendPositionChange = (value) => setLegendPosition(value);

  return (
    <Box
      sx={{
        minHeight: '100vh',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        bgcolor: 'transparent',
        p: { xs: 1, sm: 2, md: 3 }, // Responsive padding
      }}
    >
      <Card
        sx={{
          p: { xs: 2, sm: 3 },
          boxShadow: 3,
          borderRadius: 2,
          width: '100%',
          maxWidth: { xs: '100%', sm: 800, md: 1200 }, // Responsive maxWidth
          mb: 4,
        }}
      >
        <Typography
          variant="h2"
          align="center"
          sx={{
            color: theme.palette.primary.main,
            fontWeight: 'bold',
            mb: 2,
            fontSize: { xs: '1.5rem', sm: '2rem', md: '2.5rem' }, // Responsive font size
          }}
        >
          View Examples
        </Typography>
        <Typography
          variant="h6"
          gutterBottom
          sx={{
            fontWeight: 'bold',
            color: theme.palette.text.primary,
            fontSize: { xs: '1rem', sm: '1.25rem' }, // Responsive font size
          }}
        >
          Sample Visualizations
        </Typography>
        <Grid container spacing={{ xs: 2, md: 3 }}>
          <Grid item xs={12} md={6}>
            <GraphControls
              graphTitle={graphTitle}
              setGraphTitle={setGraphTitle}
              xAxisLabel={xAxisLabel}
              setXAxisLabel={setXAxisLabel}
              yAxisLabel={yAxisLabel}
              setYAxisLabel={setYAxisLabel}
              actualColor={actualColor}
              setActualColor={setActualColor}
              predictedColor={predictedColor}
              setPredictedColor={setPredictedColor}
              pointSize={pointSize}
              setPointSize={setPointSize}
              graphType={graphType}
              setGraphType={handleGraphTypeChange}
              setGraphData={setGraphData}
              graphData={graphData}
              xTickStep={xTickStep}
              setXTickStep={handleXTickStepChange}
              yTickStep={yTickStep}
              setYTickStep={handleYTickStepChange}
              showGrid={showGrid}
              setShowGrid={handleShowGridChange}
              legendPosition={legendPosition}
              setLegendPosition={handleLegendPositionChange}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              <CardMedia
                component="img"
                image="/images/image.png"
                alt="Sample Line Graph"
                sx={{
                  borderRadius: 2,
                  boxShadow: 3,
                  height: { xs: 150, sm: 200 }, // Responsive height
                  width: '100%',
                  objectFit: 'cover',
                }}
              />
              <CardMedia
                component="img"
                image="/images/image1.png"
                alt="Sample Bar Graph"
                sx={{
                  borderRadius: 2,
                  boxShadow: 3,
                  height: { xs: 150, sm: 200 }, // Responsive height
                  width: '100%',
                  objectFit: 'cover',
                }}
              />
            </Box>
          </Grid>
        </Grid>
      </Card>
      <Box sx={{ width: '100%', maxWidth: { xs: '100%', sm: 800, md: 1200 } }}>
        <Card sx={{ p: 2, boxShadow: 3, borderRadius: 2, width: '100%' }}>
          <CardContent>
            <Graph graphData={graphData} graphTitle={graphTitle} xAxisLabel={xAxisLabel} yAxisLabel={yAxisLabel} />
          </CardContent>
        </Card>
      </Box>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end',
          mt: 2,
          width: '100%',
          maxWidth: { xs: '100%', sm: 800, md: 1200 },
          px: { xs: 1, sm: 0 },
        }}
      >
        <Button
          variant="outlined"
          color="secondary"
          onClick={onBackToHome}
          sx={{
            fontSize: { xs: '0.8rem', sm: '1rem' }, // Responsive button text
            px: { xs: 2, sm: 3 }, // Responsive padding
          }}
        >
          Back to Home
        </Button>
      </Box>
    </Box>
  );
};

export default ViewExamples;