import React from 'react';
import {
  Grid,
  TextField,
  Typography,
  InputAdornment,
  Box,
  Slider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Checkbox,
  FormControlLabel,
} from '@mui/material';

const GraphControls = ({
  graphTitle,
  setGraphTitle,
  xAxisLabel,
  setXAxisLabel,
  yAxisLabel,
  setYAxisLabel,
  actualColor,
  setActualColor,
  predictedColor,
  setPredictedColor,
  pointSize,
  setPointSize,
  graphType,
  setGraphType,
  setGraphData,
  graphData,
  onRatioChange,
  onColumnSizeChange,
  xTickStep,
  setXTickStep,
  yTickStep,
  setYTickStep,
  showGrid,
  setShowGrid,
  legendPosition,
  setLegendPosition,
}) => {
  const [xInterval, setXInterval] = React.useState('');
  const [yInterval, setYInterval] = React.useState('');
  const [trainRatio, setTrainRatio] = React.useState('80');
  const [columnSize, setColumnSize] = React.useState('500');

  const originalDataRef = React.useRef(null);

  React.useEffect(() => {
    if (graphData && !originalDataRef.current) {
      originalDataRef.current = { ...graphData.data };
    }
  }, [graphData]); // Added graphData to dependencies

  const handleXIntervalChange = (value) => {
    const interval = parseFloat(value) || 1;
    setXInterval(interval);
    if (graphData && originalDataRef.current) {
      const labels = originalDataRef.current.labels;

      const numericLabels = labels.map((label, i) => {
        if (typeof label === 'number' && !isNaN(label)) return label;
        return i;
      });

      const minX = Math.min(...numericLabels.filter(num => !isNaN(num)));
      const maxX = Math.max(...numericLabels.filter(num => !isNaN(num)));
      if (isNaN(minX) || isNaN(maxX)) {
        const currentLength = originalDataRef.current.labels.length;
        const newLabels = Array.from({ length: currentLength }, (_, i) => i * interval);
        setGraphData((prev) => ({
          ...prev,
          data: { ...prev.data, labels: newLabels },
        }));
      } else {
        const currentLength = originalDataRef.current.labels.length;
        const range = maxX - minX;
        const step = (range / (currentLength - 1)) * interval;
        const newLabels = Array.from({ length: currentLength }, (_, i) => minX + i * step);
        setGraphData((prev) => ({
          ...prev,
          data: { ...prev.data, labels: newLabels },
        }));
      }
    }
  };

  const handleYIntervalChange = (value) => {
    const interval = parseFloat(value) || 1;
    setYInterval(interval);
    if (graphData && originalDataRef.current) {
      const originalActual = originalDataRef.current.datasets[0].data;
      const originalPredicted = originalDataRef.current.datasets[1].data;

      const validActual = originalActual.filter(val => !isNaN(val));
      const validPredicted = originalPredicted.filter(val => !isNaN(val));
      const allYValues = [...validActual, ...validPredicted];
      if (allYValues.length === 0) return;

      const minY = Math.min(...allYValues);

      const newActual = originalActual.map(val => {
        if (isNaN(val)) return val;
        return minY + (val - minY) * interval;
      });
      const newPredicted = originalPredicted.map(val => {
        if (isNaN(val)) return val;
        return minY + (val - minY) * interval;
      });

      setGraphData((prev) => ({
        ...prev,
        data: {
          ...prev.data,
          datasets: [
            { ...prev.data.datasets[0], data: newActual },
            { ...prev.data.datasets[1], data: newPredicted },
          ],
        },
      }));
    }
  };

  const handleRatioChange = (value) => {
    const ratio = parseFloat(value) || 80;
    if (ratio >= 0 && ratio <= 100) {
      setTrainRatio(ratio);
      if (onRatioChange) {
        onRatioChange(ratio);
      }
    }
  };

  const handleColumnSizeChange = (value) => {
    const size = parseInt(value) || 500;
    if (size >= 1) {
      setColumnSize(size);
      if (onColumnSizeChange) {
        onColumnSizeChange(size);
      }
    }
  };

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={6}>
        <TextField
          label="Graph Title"
          value={graphTitle}
          onChange={(e) => setGraphTitle(e.target.value)}
          fullWidth
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12} sm={3}>
        <TextField
          label="X-Axis Label"
          value={xAxisLabel}
          onChange={(e) => setXAxisLabel(e.target.value)}
          fullWidth
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12} sm={3}>
        <TextField
          label="Y-Axis Label"
          value={yAxisLabel}
          onChange={(e) => setYAxisLabel(e.target.value)}
          fullWidth
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12} sm={4}>
        <TextField
          label="Actual Values Color"
          type="color"
          value={actualColor}
          onChange={(e) => {
            setActualColor(e.target.value);
            setGraphData((prev) => ({
              ...prev,
              data: {
                ...prev.data,
                datasets: [
                  { ...prev.data.datasets[0], borderColor: e.target.value, backgroundColor: e.target.value },
                  prev.data.datasets[1],
                ],
              },
            }));
          }}
          fullWidth
          InputProps={{ startAdornment: <InputAdornment position="start"><Box sx={{ width: 20, height: 20, bgcolor: actualColor, borderRadius: 1 }} /></InputAdornment> }}
        />
      </Grid>
      <Grid item xs={12} sm={4}>
        <TextField
          label="Predicted Values Color"
          type="color"
          value={predictedColor}
          onChange={(e) => {
            setPredictedColor(e.target.value);
            setGraphData((prev) => ({
              ...prev,
              data: {
                ...prev.data,
                datasets: [
                  prev.data.datasets[0],
                  { ...prev.data.datasets[1], borderColor: e.target.value, backgroundColor: e.target.value },
                ],
              },
            }));
          }}
          fullWidth
          InputProps={{ startAdornment: <InputAdornment position="start"><Box sx={{ width: 20, height: 20, bgcolor: predictedColor, borderRadius: 1 }} /></InputAdornment> }}
        />
      </Grid>
      {graphType === 'scatter' && (
        <Grid item xs={12} sm={4}>
          <Typography gutterBottom>Point Size</Typography>
          <Slider
            value={pointSize}
            onChange={(e, newValue) => {
              setPointSize(newValue);
              setGraphData((prev) => ({
                ...prev,
                data: {
                  ...prev.data,
                  datasets: prev.data.datasets.map((dataset) => ({ ...dataset, pointRadius: newValue })),
                },
              }));
            }}
            min={1}
            max={20}
            step={1}
            valueLabelDisplay="auto"
          />
        </Grid>
      )}

      <Grid item xs={12} sm={4}>
        <FormControl fullWidth variant="outlined">
          <InputLabel>Graph Type</InputLabel>
          <Select
            value={graphType}
            onChange={(e) => setGraphType(e.target.value)}
            label="Graph Type"
          >
            <MenuItem value="line">Line Graph</MenuItem>
            <MenuItem value="bar">Bar Graph</MenuItem>
            <MenuItem value="scatter">Scatter Graph</MenuItem>
            <MenuItem value="area">Area Graph</MenuItem>
          </Select>
        </FormControl>
      </Grid>

      <Grid item xs={12} sm={4}>
        <Typography gutterBottom>X-Axis Tick Step</Typography>
        <Slider
          value={xTickStep}
          onChange={(e, newValue) => setXTickStep(newValue)}
          min={1}
          max={10}
          step={1}
          valueLabelDisplay="auto"
          marks={[{ value: 1, label: '1' }, { value: 5, label: '5' }, { value: 10, label: '10' }]}
        />
      </Grid>

      <Grid item xs={12} sm={4}>
        <Typography gutterBottom>Y-Axis Tick Step</Typography>
        <Slider
          value={yTickStep}
          onChange={(e, newValue) => setYTickStep(newValue)}
          min={0.1}
          max={5}
          step={0.1}
          valueLabelDisplay="auto"
          marks={[{ value: 0.1, label: '0.1' }, { value: 2.5, label: '2.5' }, { value: 5, label: '5' }]}
        />
      </Grid>

      <Grid item xs={12} sm={4}>
        <FormControlLabel
          control={<Checkbox checked={showGrid} onChange={(e) => setShowGrid(e.target.checked)} />}
          label="Show Grid"
        />
      </Grid>

      <Grid item xs={12} sm={4}>
        <FormControl fullWidth variant="outlined">
          <InputLabel>Legend Position</InputLabel>
          <Select
            value={legendPosition}
            onChange={(e) => setLegendPosition(e.target.value)}
            label="Legend Position"
          >
            <MenuItem value="top">Top</MenuItem>
            <MenuItem value="bottom">Bottom</MenuItem>
            <MenuItem value="left">Left</MenuItem>
            <MenuItem value="right">Right</MenuItem>
          </Select>
        </FormControl>
      </Grid>

      {graphData && (
        <Grid item xs={12}>
          <Typography variant="subtitle1" gutterBottom sx={{ fontWeight: 'bold', mt: 2 }}>
            Set Graph Parameters
          </Typography>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={4}>
              <TextField
                label="X-Axis Interval"
                type="number"
                value={xInterval}
                onChange={(e) => handleXIntervalChange(e.target.value)}
                fullWidth
                variant="outlined"
                placeholder="e.g., 1"
                helperText="Enter multiplier for X-axis (e.g., 1 for original, 2 for double range)"
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                label="Y-Axis Interval"
                type="number"
                value={yInterval}
                onChange={(e) => handleYIntervalChange(e.target.value)}
                fullWidth
                variant="outlined"
                placeholder="e.g., 1"
                helperText="Enter multiplier for Y-axis (e.g., 1 for original, 2 for double range)"
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                label="Train Ratio (%)"
                type="number"
                value={trainRatio}
                onChange={(e) => handleRatioChange(e.target.value)}
                fullWidth
                variant="outlined"
                placeholder="e.g., 80"
                helperText="Enter train percentage (0-100)"
                inputProps={{ min: 0, max: 100 }}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                label="Max Columns"
                type="number"
                value={columnSize}
                onChange={(e) => handleColumnSizeChange(e.target.value)}
                fullWidth
                variant="outlined"
                placeholder="e.g., 500"
                helperText="Enter max columns for testing (default 500)"
                inputProps={{ min: 1 }}
              />
            </Grid>
          </Grid>
        </Grid>
      )}
    </Grid>
  );
};

export default GraphControls;