// src/components/Form.js
import React from 'react';
import { Grid, TextField, Button, Select, MenuItem, FormControl, InputLabel, CircularProgress, Box, useTheme, useMediaQuery, Paper, Typography } from '@mui/material';
import { BarChart } from '@mui/icons-material';

const Form = ({ onSubmit, file, setFile, target, setTarget, model, setModel, graphType, setGraphType, loading }) => {
  const theme = useTheme(); // Added useTheme hook to detect dark/light mode
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

  return (
    <Paper elevation={8} sx={{
      p: isMobile ? 2 : isTablet ? 3 : 4,
      bgcolor: theme.palette.background.paper, // Dynamic background based on theme
      borderRadius: 3,
      boxShadow: '0 8px 25px rgba(0, 0, 0, 0.15)',
      color: theme.palette.text.primary, // Dynamic text color based on theme
      transition: 'transform 0.3s, box-shadow 0.3s',
      '&:hover': { transform: 'scale(1.02)', boxShadow: '0 12px 30px rgba(0, 0, 0, 0.2)' },
    }}>
      <Typography
        variant="h4"
        align="center"
        sx={{ color: theme.palette.primary.main, fontWeight: 'bold', mb: 3, mt: 2 }} // Dynamic heading color
      >
        DataTrain Visualizer 🚀
      </Typography>
      <Grid container spacing={isMobile ? 2 : 3}>
        <Grid item xs={12}>
          <Box sx={{ mb: 2, display: 'flex', alignItems: 'center' }}>
            <input
              type="file"
              accept=".csv"
              onChange={(e) => { console.log('File selected:', e.target.files[0]); setFile(e.target.files[0]); }}
              style={{ display: 'none' }}
              id="file-upload"
            />
            <label htmlFor="file-upload">
              <Button
                variant="contained"
                component="span"
                color="primary"
                startIcon={<BarChart />}
                sx={{
                  width: '100%',
                  padding: isMobile ? '10px 16px' : '12px 24px',
                  borderRadius: 2,
                  background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.primary.light})`, // Dynamic gradient
                  boxShadow: '0 4px 15px rgba(25, 118, 210, 0.4)',
                  color: theme.palette.text.primary, // Dynamic text color
                  '&:hover': {
                    background: `linear-gradient(45deg, ${theme.palette.primary.dark}, ${theme.palette.primary.main})`,
                    transform: 'scale(1.05)',
                    transition: 'transform 0.3s, background 0.3s',
                  },
                  textTransform: 'none',
                  fontSize: isMobile ? '14px' : '16px',
                }}
              >
                {file ? file.name : 'Choose File'}
              </Button>
            </label>
          </Box>
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            label="Target Column"
            value={target}
            onChange={(e) => { console.log('Target changed:', e.target.value); setTarget(e.target.value); }}
            fullWidth
            variant="outlined"
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 2,
                backgroundColor: theme.palette.mode === 'dark' ? '#424242' : '#f9f9f9', // Dynamic input background
                '&:hover fieldset': { borderColor: theme.palette.primary.main },
                '&.Mui-focused fieldset': { borderColor: theme.palette.primary.main, boxShadow: `0 0 5px ${theme.palette.primary.main}40` },
              },
              '& .MuiInputLabel-root': { color: theme.palette.text.secondary }, // Dynamic label color
              '& .MuiInputBase-input': { color: theme.palette.text.primary }, // Dynamic input text color
            }}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth variant="outlined" sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2, backgroundColor: theme.palette.mode === 'dark' ? '#424242' : '#f9f9f9' } }}>
            <InputLabel sx={{ color: theme.palette.text.secondary }}>Model</InputLabel> {/* Dynamic label color */}
            <Select
              value={model}
              onChange={(e) => { console.log('Model changed:', e.target.value); setModel(e.target.value); }}
              label="Model"
              sx={{
                '& .MuiSelect-select': { color: theme.palette.text.primary, backgroundColor: theme.palette.mode === 'dark' ? '#424242' : '#f9f9f9' }, // Dynamic select text and background
                '& .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.mode === 'dark' ? '#616161' : '#e0e0e0' }, // Dynamic border color
                '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.primary.main },
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.primary.main, boxShadow: `0 0 5px ${theme.palette.primary.main}40` },
              }}
            >
              <MenuItem value="linear_regression">Linear Regression</MenuItem>
              <MenuItem value="logistic_regression">Logistic Regression</MenuItem>
              <MenuItem value="random_forest_regressor">Random Forest Regressor</MenuItem>
              <MenuItem value="random_forest_classifier">Random Forest Classifier</MenuItem>
              <MenuItem value="svm_regressor">SVM Regressor</MenuItem>
              <MenuItem value="svm_classifier">SVM Classifier</MenuItem>
              <MenuItem value="kmeans">K-Means Clustering</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth variant="outlined" sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2, backgroundColor: theme.palette.mode === 'dark' ? '#424242' : '#f9f9f9' } }}>
            <InputLabel sx={{ color: theme.palette.text.secondary }}>Graph Type</InputLabel> {/* Dynamic label color */}
            <Select
              value={graphType}
              onChange={(e) => { console.log('Graph Type changed:', e.target.value); setGraphType(e.target.value); }}
              label="Graph Type"
              sx={{
                '& .MuiSelect-select': { color: theme.palette.text.primary, backgroundColor: theme.palette.mode === 'dark' ? '#424242' : '#f9f9f9' }, // Dynamic select text and background
                '& .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.mode === 'dark' ? '#616161' : '#e0e0e0' }, // Dynamic border color
                '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.primary.main },
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: theme.palette.primary.main, boxShadow: `0 0 5px ${theme.palette.primary.main}40` },
              }}
            >
              <MenuItem value="line">Line Graph</MenuItem>
              <MenuItem value="bar">Bar Graph</MenuItem>
              <MenuItem value="scatter">Scatter Graph</MenuItem>
              <MenuItem value="area">Area Graph</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12}>
          <Button
            variant="contained"
            color="primary"
            onClick={(e) => { console.log('Train Model clicked in Form'); onSubmit(e); }}
            disabled={loading || !file || !target}
            fullWidth
            sx={{
              padding: isMobile ? '10px' : '12px',
              fontSize: isMobile ? '14px' : '16px',
              borderRadius: 2,
              background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.primary.light})`, // Dynamic gradient
              boxShadow: '0 6px 20px rgba(25, 118, 210, 0.5)',
              color: theme.palette.text.primary, // Dynamic text color
              '&:hover': {
                background: `linear-gradient(45deg, ${theme.palette.primary.dark}, ${theme.palette.primary.main})`,
                transform: 'scale(1.05)',
                transition: 'transform 0.3s, background 0.3s',
              },
              '&:disabled': {
                background: theme.palette.mode === 'dark' ? '#616161' : '#e0e0e0', // Dynamic disabled background
                color: theme.palette.mode === 'dark' ? '#b0b0b0' : '#9e9e9e', // Dynamic disabled text
                boxShadow: 'none',
              },
            }}
          >
            {loading ? <CircularProgress size={24} sx={{ color: theme.palette.text.primary }} /> : 'Train Model'}
          </Button>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default Form;