// src/App.js
import React, { useState} from 'react';
import axios from 'axios';
import { Card, CardContent, Box, Typography, Button, CssBaseline, IconButton, Switch } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Brightness4, Brightness7} from '@mui/icons-material';
import Home from './components/Home';
import Form from './components/Form';
import Graph from './components/Graph';
import GraphControls from './components/GraphControls';
import About from './components/About';
import ViewExamples from './components/ViewExamples'; // Import the ViewExamples component

function App() {
  const [view, setView] = useState('home');
  const [darkMode, setDarkMode] = useState(false); // State for dark mode toggle
  const [file, setFile] = useState(null);
  const [target, setTarget] = useState('');
  const [model, setModel] = useState('linear_regression');
  const [graphType, setGraphType] = useState('line');
  const [graphData, setGraphData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [trainRatio, setTrainRatio] = useState(80);
  const [maxColumns, setMaxColumns] = useState(500);
  const [graphTitle, setGraphTitle] = useState('Training Results');
  const [xAxisLabel, setXAxisLabel] = useState('Index');
  const [yAxisLabel, setYAxisLabel] = useState('Values');
  const [actualColor, setActualColor] = useState('#1976d2');
  const [predictedColor, setPredictedColor] = useState('#d32f2f');
  const [pointSize, setPointSize] = useState(3);
  const [xTickStep, setXTickStep] = useState(5);
  const [yTickStep, setYTickStep] = useState(0.5);
  const [legendPosition, setLegendPosition] = useState('top');
  const [showGrid, setShowGrid] = useState(true);

  // Debug state changes
  React.useEffect(() => {
    console.log('Current state:', { view, darkMode, file, target, model, graphType, graphData, loading, trainRatio, maxColumns });
  }, [view, darkMode, file, target, model, graphType, graphData, loading, trainRatio, maxColumns]);

  // Create theme based on dark mode state
  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: '#1976d2',
      },
      secondary: {
        main: '#4a148c',
      },
      background: {
        default: darkMode ? '#1a1a1a' : '#f5f5f5',
        paper: darkMode ? '#303030' : 'white',
      },
      text: {
        primary: darkMode ? '#ffffff' : '#000000',
        secondary: darkMode ? '#b0b0b0' : '#666',
      },
    },
    components: {
      MuiPaper: {
        styleOverrides: {
          root: {
            backgroundColor: darkMode ? '#303030' : 'white',
            color: darkMode ? '#ffffff' : '#000000',
          },
        },
      },
      MuiCard: {
        styleOverrides: {
          root: {
            backgroundColor: darkMode ? '#424242' : 'white',
            color: darkMode ? '#ffffff' : '#000000',
          },
        },
      },
    },
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('Train Model button clicked in App.js');
    console.log('File:', file);
    console.log('Target:', target);
    console.log('Model:', model);
    console.log('Graph Type:', graphType);
    console.log('Loading state:', loading);

    setLoading(true);

    if (!file || !target) {
      console.error('File or Target is missing');
      alert('Please upload a file and specify a target column!');
      setLoading(false);
      return;
    }

    const formData = new FormData();
    formData.append('file', file);
    formData.append('target', target);
    formData.append('model', model);
    formData.append('graph', graphType);
    formData.append('train_ratio', trainRatio / 100);
    formData.append('max_columns', maxColumns);

    try {
      console.log('Sending request to backend at http://localhost:5000/train...');
      const response = await axios.post('http://localhost:5000/train', formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      console.log('Backend response:', response.data);

      const { train_x_data, train_y_data, test_x_data, test_y_data, train_predictions } = response.data;

      // Debug backend response
      console.log('Backend response data:', {
        train_x_data,
        train_y_data,
        test_x_data,
        test_y_data,
        train_predictions,
      });

      if (!train_x_data || !train_y_data || !test_x_data || !train_predictions) { // Removed test_y_data check since we're not using test data
        console.error('Missing required data in backend response');
        alert('Backend response is missing required data for graphing!');
        setLoading(false);
        return;
      }

      const ensureNumeric = (data) => {
        console.log('Ensuring numeric for data:', data.slice(0, 5)); // Log first few values for debugging
        return data.map(val => (typeof val === 'number' && !isNaN(val) ? val : 0));
      };

      const trainX = ensureNumeric(train_x_data);
      const trainY = ensureNumeric(train_y_data);
      const trainPred = ensureNumeric(train_predictions);

      console.log('Processed data:', {
        trainX: trainX.slice(0, 5),
        trainY: trainY.slice(0, 5),
        trainPred: trainPred.slice(0, 5),
      });

      // Dynamic step size for uniform sampling based on data length
      const totalPoints = trainX.length;
      const desiredPoints = Math.min(50, totalPoints); // Limit to 50 points for readability, adjust as needed
      const step = Math.max(1, Math.floor(totalPoints / desiredPoints));

      const sampleData = (data) => {
        return data.filter((_, i) => i % step === 0);
      };

      const sampledTrainX = sampleData(trainX);
      const sampledTrainY = sampleData(trainY);
      const sampledTrainPred = sampleData(trainPred);

      const trainLabels = sampledTrainX.map((x, i) => `Train ${i * step}`);

      const prepareScatterData = (xValues, yValues) => {
        return xValues.map((x, i) => ({ x, y: yValues[i] }));
      };

      let data;
      if (graphType === 'scatter') {
        data = {
          datasets: [
            { label: 'Actual (Train)', data: prepareScatterData(sampledTrainX, sampledTrainY), borderColor: '#1976d2', backgroundColor: '#1976d2', pointRadius: pointSize, type: 'scatter' },
            { label: 'Predicted (Train)', data: prepareScatterData(sampledTrainX, sampledTrainPred), borderColor: '#d32f2f', backgroundColor: '#d32f2f', pointRadius: pointSize, type: 'scatter' },
          ],
        };
      } else if (graphType === 'area') {
        data = {
          labels: trainLabels,
          datasets: [
            { label: 'Actual (Train)', data: sampledTrainY, borderColor: '#1976d2', backgroundColor: 'rgba(25, 118, 210, 0.2)', fill: true, pointRadius: pointSize, tension: 0.1, type: 'line' },
            { label: 'Predicted (Train)', data: sampledTrainPred, borderColor: '#d32f2f', backgroundColor: 'rgba(211, 47, 47, 0.2)', fill: true, pointRadius: pointSize, tension: 0.1, type: 'line' },
          ],
        };
      } else {
        data = {
          labels: trainLabels,
          datasets: [
            { label: 'Actual (Train)', data: sampledTrainY, borderColor: '#1976d2', backgroundColor: '#1976d2', fill: false, pointRadius: pointSize, tension: 0.1, type: graphType },
            { label: 'Predicted (Train)', data: sampledTrainPred, borderColor: '#d32f2f', backgroundColor: '#d32f2f', fill: false, pointRadius: pointSize, tension: 0.1, type: graphType },
          ],
        };
      }

      console.log('Setting graph data:', data);
      setGraphData({
        data,
        type: graphType === 'area' ? 'line' : graphType,
        options: {
          scales: {
            x: { ticks: { stepSize: xTickStep }, grid: { drawOnChartArea: showGrid } },
            y: { ticks: { stepSize: yTickStep }, grid: { drawOnChartArea: showGrid } },
          },
          plugins: { legend: { position: legendPosition } },
          elements: { line: { fill: graphType === 'area' } },
        },
      });
      console.log('Graph data set, rendering graph below form...');
    } catch (error) {
      console.error('Error in API call:', error.response ? error.response.data : error.message);
      alert(error.response?.data?.error || 'Something went wrong while training the model!');
    } finally {
      setLoading(false);
    }
  };

  const handleRatioChange = (ratio) => setTrainRatio(ratio);
  const handleColumnSizeChange = (size) => setMaxColumns(size);
  const handleXTickStepChange = (value) => setXTickStep(value);
  const handleYTickStepChange = (value) => setYTickStep(value);
  const handleShowGridChange = (value) => setShowGrid(value);
  const handleLegendPositionChange = (value) => setLegendPosition(value);
  const handleGraphTypeChange = (value) => setGraphType(value);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline /> {/* Ensures consistent baseline styling */}
      <Box sx={{ minHeight: '100vh', width: '100vw' }}> {/* Full-screen wrapper for all views */}
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', mb: 2 }}>
          <IconButton onClick={() => setDarkMode(!darkMode)} color="inherit">
            {darkMode ? <Brightness7 /> : <Brightness4 />}
            <Switch checked={darkMode} onChange={() => setDarkMode(!darkMode)} />
          </IconButton>
        </Box>
        {view === 'home' && (
          <Home 
            onGetStarted={() => setView('form')} 
            onViewExamples={() => setView('graph')} 
            onViewAbout={() => setView('about')} 
          />
        )}
        {view === 'form' && (
          <Box>
            <Card sx={{ p: 3, boxShadow: 3, borderRadius: 2, mb: 4 }}>
              <Form
                onSubmit={(e) => handleSubmit(e)}
                file={file}
                setFile={setFile}
                target={target}
                setTarget={setTarget}
                model={model}
                setModel={setModel}
                graphType={graphType}
                setGraphType={handleGraphTypeChange}
                loading={loading}
              />
            </Card>
            {graphData && (
              <>
                <Card sx={{ p: 3, boxShadow: 3, borderRadius: 2, mb: 4 }}>
                  <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold', color: theme.palette.text.primary }}>
                    Customize Your Graph
                  </Typography>
                  <GraphControls
                    graphTitle={graphTitle}
                    setGraphTitle={setGraphTitle}
                    xAxisLabel={xAxisLabel}
                    setXAxisLabel={setXAxisLabel}
                    yAxisLabel={yAxisLabel}
                    setYAxisLabel={setYAxisLabel}
                    actualColor={actualColor}
                    setActualColor={setActualColor}
                    predictedColor={predictedColor}
                    setPredictedColor={setPredictedColor}
                    pointSize={pointSize}
                    setPointSize={setPointSize}
                    graphType={graphType}
                    setGraphType={handleGraphTypeChange}
                    setGraphData={setGraphData}
                    graphData={graphData}
                    onRatioChange={handleRatioChange}
                    onColumnSizeChange={handleColumnSizeChange}
                    xTickStep={xTickStep}
                    setXTickStep={handleXTickStepChange}
                    yTickStep={yTickStep}
                    setYTickStep={handleYTickStepChange}
                    showGrid={showGrid}
                    setShowGrid={handleShowGridChange}
                    legendPosition={legendPosition}
                    setLegendPosition={handleLegendPositionChange}
                  />
                </Card>
                <Box>
                  <Card sx={{ p: 2, boxShadow: 3, borderRadius: 2 }}>
                    <CardContent>
                      <Graph graphData={graphData} graphTitle={graphTitle} xAxisLabel={xAxisLabel} yAxisLabel={yAxisLabel} />
                    </CardContent>
                  </Card>
                </Box>
              </>
            )}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mb: 2 }}>
              <Button variant="outlined" color="secondary" onClick={() => setView('home')} sx={{ mr: 2, color: theme.palette.text.primary, borderColor: theme.palette.secondary.main }}>
                Back to Home
              </Button>
            </Box>
          </Box>
        )}
        {view === 'graph' && (
          <ViewExamples onBackToHome={() => setView('home')} /> // Render ViewExamples component directly
        )}
        {view === 'about' && (
          <About onBackToHome={() => setView('home')} /> // Already exists
        )}
      </Box>
    </ThemeProvider>
  );
}

export default App;